.productsContainer{ 
    border-top: 1px dashed $brand-lightGrey;
    padding: 50px 20px;
    // margin: 5% 2%;
    display: grid;
    grid-gap: 25px;
    grid-template-columns: repeat(4, minmax(250px, 250px));
    grid-template-rows: repeat(4, 288px);

    .products{
        display: flex;
        flex-direction: column;
        // justify-content: center;
       
        .productsThumbnail{
            background-color: #DCDCDC;
            width: 252px;

            img{
            width: 250px;
            height: 250px;
            object-fit: cover;
            border: 1px solid #DCDCDC;        
            }
        
        }
    
        .productsInfo{
            border: 1px solid #DCDCDC;
            padding: 10px 5px;
            text-align: center;
            width: 240px;
            font-family: 'Merriweather Web Light';

        }

    }
    
}